import * as React from "react";
import { EditorPropsBase } from "../MyCanvasEditor";
import { Subscribers, EvenType } from "../Subscribers";
import { Button, ButtonTitle, ButtonIcon } from "./Button";
import { CacheManager, CacheType } from "../CacheManager";

interface ISaveProjectProps extends EditorPropsBase {
	onClick: () => void;
}
interface ISaveProjectState {
	isDirty: boolean;
	functionBeforeUnload: EventListenerOrEventListenerObject;
}
declare let window: MyWindow;

export class SaveProject extends React.Component<ISaveProjectProps, ISaveProjectState> {
	constructor() {
		super();
		this.state = {
			isDirty: false,
			functionBeforeUnload: (e: BeforeUnloadEvent) => {
				const message = "Do you want to leave this site?";
				e.returnValue = message;
				return message;
			}};
		Subscribers.AddSubscriber("Pages", EvenType.DocumentDirtyStateChanged, this, this.updateDirtyState.bind(this));
	}

	updateDirtyState() {
		let dirty = this.stateDirty();
		this.setState({ isDirty: dirty });

		window.removeEventListener("beforeunload", this.state.functionBeforeUnload);
		if (dirty)
			window.addEventListener("beforeunload", this.state.functionBeforeUnload);
	}

	stateDirty(): boolean {
		const dirtyList = CacheManager.GetDataByType(CacheType.DirtyStateProject);
		let anyDirty = dirtyList.some((v) => { return v.Data });
		let currentDirty = this.props.applicationScope.myCanvasEditor.getDirtyState();
		return anyDirty || currentDirty;
	}

	onClick() {
		this.props.onClick();
	}

	render() {
		return (
            <Button icon={ButtonIcon.SaveProject} isHeader={true} title={ButtonTitle.SaveProject} enabled={this.state.isDirty} onClick={this.onClick.bind(this)} />
		);
	}
}
