import * as React from "react";
import { CollapseItem } from "../components/CollapseItem";

interface PanelProps {
    id: string;
    title: string;
    onExpanded?: (panel: Panel) => void;
    disabled?: boolean;
    hidden?: boolean;
}
interface PanelState {
    height?: number;
    width?: number;
}
interface PanelRefs {
    collapseItem?: CollapseItem;
}
export class Panel extends React.Component<PanelProps, PanelState> {
    objs: PanelRefs = { collapseItem: null };
    constructor() {
        super();
        this.state = {
            height: null
        };
    }
    handleCollapsed() {
        this.setHeight(null);
    }
    handleExpanded() {
        if (this.props.onExpanded) {
            this.props.onExpanded(this);
        }
    }
    collapse() {
        this.setHeight(null);
        this.objs.collapseItem.collapse(true);
    }
    setHeight(height: number, width?: number) {
        this.setState({
            height: height,
            width: width
        });
    }
    render() {
        return (
            <div id={this.props.id} className={`panel ${ this.props.hidden ? "hidden" : ""}`} style={this.state.height || this.state.width ? { height: this.state.height, width: this.state.width } : null}>
                <CollapseItem ref={(x: CollapseItem) => this.objs.collapseItem = x}
                    onExpanded={this.handleExpanded.bind(this)} onCollapsed={this.handleCollapsed.bind(this)} hideToggler={this.props.disabled}
                    header={
                        <div className="panel-header">{this.props.title}</div>
                    }
                    body={
                        <div className="panel-body">{this.props.children}</div>
                    } />
            </div>);
    }
}
